.CreateTodoButton{
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(152, 65, 173, 0.5);
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color:#FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px; 
    z-index: 1;

    transform: rotate(0);
    transition: .3s ease;
}

.CreateTodoButton:hover{
    transform: rotate(360deg);
}